.col-sm-6 {
  min-height: 500px;
  background: lightgrey;
  text-align: center;
}

.image-wrap {
  display: inline-block;
  position: relative;
  min-height: 300px;
  min-width: 300px;
  width: 30vh;
  height: 30vh;
  margin: 15px;
}

.image-wrap .overlay {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  color: white;
  font: 30px sans-serif;
  font-weight: bold;
  opacity: 0;
  transition: opacity .5s ease;
  background-color: black;
  padding-top: 100px;
  border-radius: 10px;
}

.image-wrap:hover .overlay {
  opacity: 0.9;
}

.menu-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("//images.unsplash.com/photo-1500099817043-86d46000d58f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80");
}

#element-with-background-image {
   position: relative;
   background-image: url("//spin.atomicobject.com/wp-content/uploads/20170324102432/portfolio-tips-feature-image.jpg");
   min-height: 300px;
   min-width: 300px;
   width: 30vh;
   height: 30vh;
   margin: 15px;
}

.landing-page-main-container {
  height: 100vh;
  width: 100vw;
  padding-left: 5vw;
}

.left-align {
  text-align: left;
  padding: 0px 15px 0px 15px;
}
